$font-weight-label: 500;
$size-icon: 40px;
$accent-color: #00acdc;
$card-border-color: #f3f3f3;
$white-color: white;
$min-width-catalog: 1024px;
$max-width-catalog: 1200px;

@mixin font-small-thin {
  font-size: 11px;
  line-height: 13px;
}

@mixin font-middle {
  font-size: 12px;
  line-height: 15px;
  font-weight: 500;
}

@mixin font-large-bold {
  font-size: 16px;
  line-height: 19px;
  font-weight: bold;
}
